import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'
import store from './store'
import ws from "@/utils/socketapi"

import 'font-awesome/css/font-awesome.css';

import {postRequest} from "@/utils/api";
import {getRequest} from "@/utils/api";
import {putRequest} from "@/utils/api";
import {deleteRequest} from "@/utils/api";
import {postKeyValueRequest} from "@/utils/api";

import '@/assets/css/container.css'
import '@/assets/css/header.css'
import '@/assets/css/article.css'
import '@/assets/css/side.css'
import '@/assets/css/blog.css'


Vue.use(ElementUI);
Vue.prototype.$StaticImage = 'http://39.107.26.8:4545'
Vue.prototype.$WebSocketUrl = 'ws://39.107.26.8:9100/ws'
// Vue.prototype.$WebSocketUrl = 'ws://localhost:9100/ws'
Vue.config.productionTip = false
Vue.prototype.postRequest = postRequest
Vue.prototype.putRequest = putRequest
Vue.prototype.getRequest = getRequest
Vue.prototype.deleteRequest = deleteRequest
Vue.prototype.postKeyValueRequest = postKeyValueRequest
Vue.prototype.$ws = ws

router.beforeEach((to,from, next) => {
  if (to.path == '/' || to.path == '/gobang' || to.path == '/blog' || to.path == '/register' || to.path == '/article' || to.path == '/about' ||to.path == '/messages' || to.path == '/guessDraw'){
    next();
  }
})


new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')

