import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state : {
        currentUser: JSON.parse(window.sessionStorage.getItem("user")),
    },
    mutations:{
        Init_CurrentUser(state, user){
            state.currentUser = user;
        }
    }
})

export default store;
