
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        websock: null,
        eventlist:[]
    },
    getters: {
        onEvent(state) {
            return function (method) {
                let index = state.eventlist.map((eb) => {return eb.method}).indexOf(method);
                if (state.eventlist.length > 0 && index >= 0) {
                    let result = Object.assign({}, state.eventlist[index]);
                    state.eventlist.splice(index, 1);
                    return result.data;
                }
                return null;
            }
        }
    },
    mutations: {
        WEBSOCKET_INIT(state, params) {
            let url = params.url
            // alert(url)
            state.websock = new WebSocket(url);
            state.websock.onopen = function () {
                console.log("连接成功！");
                let data = params.params
                state.websock.send(JSON.stringify(data))
            }
            state.websock.onmessage = function (callBack) {
                console.log("ws接收！");
                console.log(callBack.data);
                state.eventlist.push({
                    method:'string',
                    data:callBack.data
                })
                // alert(JSON.stringify(state.eventlist))
            }
            state.websock.οnerrοr=function(e) { //错误
                console.log("ws错误!");
                console.log(e);
            }
            state.websock.onclose=function(e) { //关闭
                console.log("ws关闭！");
                console.log(e);
            }
        },
        WEBSOCKET_SEND(state, p) {
            console.log("ws发送！");
            state.websock.send(p);
        }
    },
    actions: {
        WEBSOCKET_INIT({commit},params) {
            commit('WEBSOCKET_INIT', params)
        },
        WEBSOCKET_SEND({commit}, p) {
            commit('WEBSOCKET_SEND', p)
        }
    }
})
